
/* .desktop and .mobile classes for visibility */

@media only screen and (max-width: 890px) {
  .desktop {
    display: none;
  }
}

@media only screen and (min-width: 890px) {
  .mobile {
    display: none;
  }
}

/* header */

header {
  padding: 1rem 0.5rem;

  .ant-row-space-between {
    align-items: center;
    text-align: center;
  }
}

.ant-drawer-body {
  display: flex;
  flex-direction: column;
}

.ant-drawer-content-wrapper {
  width: 300px !important;
}

.drawer-header {
  margin: 0 1rem 2.5rem 1rem;
  font-weight: 500;
  font-size: 25px;
  color: #404041;
  text-align: left;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

header a, .ant-drawer-body a {
  font-size: 1.2rem;
  color: #18216d;
  transition: all 0.2s ease-in;
  margin: 0.5rem 2rem;
  text-align: center;

  &:hover, &:focus {
    text-decoration: rgb(255,130,92) wavy underline;
    color: rgb(255,130,92);
  }
}

/* container: the header, footer and each section at the highest level;
   adds margins based on screen size */
.container {
  position: relative;
  width: 100%;
  max-width: 1200px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 60px;

  @media only screen and (max-width: 1024px) {
    max-width: calc(100% - 68px);
    padding: 0 30px;
  }

  @media only screen and (max-width: 768px) {
    max-width: calc(100% - 38px);
    padding: 0 18px;
  }

  @media only screen and (max-width: 414px) {
    max-width: 100%;
    padding: 0 18px;
  }
}

/* content sections */

.content-section {
  position: relative;
  padding: 10rem 0 5rem;
  @media only screen and (max-width: 1024px) {
    padding: 4rem 0 4rem;
  }
  & > div {
    margin-bottom: 3rem;
  }
  &.compact {
    padding: 0;
    padding-top: 4rem;
  }
}

@media only screen and (max-width: 576px) {
  .content-section.flipped {
    flex-wrap: wrap-reverse;
  }
}

.partner .profile {
  h2, div, .ant-avatar, em {
    margin: 0 auto;
    display: block;
    width: 100%;
    text-align: center;
  }
  .ant-avatar {
    margin: 0 auto 2rem auto;
  }
  h2 {
    margin-bottom: 0.25rem;
  }
  em {
    color: #333;
  }
  .socials a {
    margin: 0 0.5rem;
  }
}

/* footer */

footer, .section-header {
  background: rgb(241,242,243);
  padding: 3rem 0;
}

.section-header h1 {
  width: 100%;
  text-align: center;
  margin: 1rem;
}
